.listCardCont {
  width: 100%;
  background-color: #242424;
  padding: 20px 35px;
  border-radius: 30px;
}
.listCardCont > p {
  margin: 0 0 20px 0;
  font-size: 25px;
  color: #fff;
  font-family: Azeret;
}

.lists ul {
  display: flex;
  flex-direction: column;
  padding: 0 15px;
  color: #fff;
  gap: 20px;
}
.lists ul li h2 {
  color: #fff;
  font-family: Tiny;
  font-size: 24px;
  letter-spacing: 3px;
}
.lists ul li p {
  padding: 10px 0;
  color: #8a8a8a;
  font-family: Azeret;
}
.lists ul li p a {
  color: #fff;
}

@media (max-width: 800px) {
  .listCardCont > p {
    font-size: 20px;
  }
  .lists ul {
    padding: 0;
  }
  .lists ul li h2 {
    font-size: 18px;
  }
  .lists ul li p {
    font-size: 12px;
  }
}
@media (max-width: 400px) {
  .lists ul {
    padding: 0;
  }
  .lists ul li h2 {
    font-size: 15px;
  }
  .lists ul li p {
    font-size: 10px;
  }
}/*# sourceMappingURL=Listcard.module.css.map */